import { useState, useEffect } from 'react';

interface PWAInstallPrompt {
  isInstallable: boolean;
  promptInstall: () => Promise<void>;
}
interface BeforeInstallPromptEvent extends Event {
  readonly platforms: string[];
  readonly userChoice: Promise<{ outcome: 'accepted' | 'dismissed' }>;
  prompt(): Promise<void>;
}


const usePWAInstallPrompt = (): PWAInstallPrompt => {
  const [deferredPrompt, setDeferredPrompt] = useState<BeforeInstallPromptEvent | null>(null);
  const [isInstallable, setIsInstallable] = useState<boolean>(false);

  useEffect(() => {
    const beforeInstallPromptHandler = (event: BeforeInstallPromptEvent) => {
      event.preventDefault();
      setDeferredPrompt(event);
      setIsInstallable(true);
    };

    window.addEventListener('beforeinstallprompt', beforeInstallPromptHandler as EventListener);

    return () => {
      window.removeEventListener('beforeinstallprompt', beforeInstallPromptHandler as EventListener);
    };
  }, []);

  const promptInstall = async () => {
    console.log('promptInstall')
    if (!deferredPrompt) {
      console.log('No deferredPrompt')
      return;
    }
    deferredPrompt.prompt();
    const { outcome } = await deferredPrompt.userChoice;
    if (outcome === 'accepted') {
      console.log('User accepted the install prompt');
    } else {
      console.log('User dismissed the install prompt');
    }
    setDeferredPrompt(null);
    setIsInstallable(false);
  };

  return { isInstallable, promptInstall };
};

export default usePWAInstallPrompt;
