import React from 'react';
import usePWAInstallPrompt from '../hooks/usePromptInstall';
import { Button } from './Button';

const InstallButton: React.FC = () => {
  const { isInstallable, promptInstall } = usePWAInstallPrompt();
  console.log(isInstallable, 'isInstallable, promptInstall')

  return (
    <div className='flex justify-between px-6 py-2'>
      {isInstallable && (
        <Button title='Install App IF' onClick={promptInstall} />
      )}
      <Button title='Install App!!!' onClick={promptInstall} />
    </div>
  );
};

export default InstallButton;